import React, { Component } from 'react'
import { input, panel } from './Panel.module.scss'
import TagGroup from '../EventList/TagGroup';

import PlacesAutocomplete, {
    geocodeByAddress,
    getLatLng,
} from 'react-places-autocomplete';

const searchOptions = {
    types: ['(cities)']
}

export default class DetailsPanel extends Component {
    constructor(props) {
        super(props);
        this.state = { location: this.props.location  && this.props.location.name ? this.props.location.name : "" };
    }

    handleLocationChange = location => {
        this.setState({ location });
    };
r
    handleSelect = location => {
        this.setState({ location }, () => {
            geocodeByAddress(location)
                .then(results => getLatLng(results[0]))
                .then(latLng => this.props.handleLocationSelect({ name: location, ...latLng}))
                .catch(error => console.error('Error', error));
        })
    }

    render() {
        let styleActive = {
            boxShadow: "0 12px 20px #d8e0e6",
            border: "solid 1px #e8e8e8",
            zIndex: 2000,
            position: "absolute",
            width: "80%",
        }

        let styleUnactive = {
            boxShadow: "none",
            border: "none",
            position: "absolute",
            width: "80%",
            zIndex: 2000,
        }

        return (
           
            <div className={panel}>
                <h1>Love suggestions? Help us, help you.</h1>

                <h2 style={{ marginTop: "20px" }}>Where ya heading?    </h2>
                <div style={{ width: "100%" }}>
                    <PlacesAutocomplete
                        value={this.state.location}
                        onChange={this.handleLocationChange}
                        onSelect={this.handleSelect}
                        searchOptions={searchOptions}
                        debounce={350}
                    >
                        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                            <div style={{ width: "100%" }}>
                                <input {...getInputProps({
                                    placeholder: 'Location',
                                    className: 'location-search-input',
                                })} placeholder="Location" className={input}></input>
                                <div className="autocomplete-dropdown-container" style={loading || suggestions.length > 0 ? styleActive : styleUnactive}>
                                    {loading && <div>Loading...</div>}
                                    {suggestions.map(suggestion => {
                                        const className = suggestion.active
                                            ? 'suggestion-item--active'
                                            : 'suggestion-item';
                                        // inline style for demonstration purpose
                                        const style = suggestion.active
                                            ? { backgroundColor: '#e6f7ff', cursor: 'pointer', padding: '.5em', fontWeight: "bold" }
                                            : { backgroundColor: '#ffffff', cursor: 'pointer', padding: '.5em' };
                                        return (
                                            <div
                                                {...getSuggestionItemProps(suggestion, {
                                                    className,
                                                    style,
                                                })}
                                            >
                                                <span>{suggestion.description}</span>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        )}
                    </PlacesAutocomplete>
                </div>
                {this.state.location && <a>More than 1 place? Add another!</a>}

                <h2 style={{ marginTop: "20px" }}>Tag Some Interests</h2>
                <TagGroup tags={this.props.tags} handleTagChange={(tags) => this.props.handleTagChange(tags)}></TagGroup>
            </div>
        )
    }
}
